
import { Component, OnInit, ElementRef } from '@angular/core';
import {GetdataService} from '../../services/getdata.service';

import {Http, Response, Headers} from '@angular/http';

import {RouterModule,ActivatedRoute, Routes} from '@angular/router';
import { routerTransition } from '../../components/routeranimation/router.animations';

import {trigger, state, animate, style, transition} from '@angular/animations';

import 'rxjs/add/operator/map';


@Component({
  selector: 'app-feproducts',
  templateUrl: './feproducts.component.html',
  styleUrls: ['./feproducts.component.css'],
  animations: [
   trigger('fadeIn', [
     transition(':enter', [
       style({ opacity: '0' }),
       animate('.5s ease-out', style({ opacity: '1' })),
     ]),
   ]),
  ]
  // animations: [routerTransition()],
  // host: {'[@routerTransition]': ''}
})
export class FeproductsComponent implements OnInit {
  product:Product[];
    public category_url:string;

  constructor(private route: ActivatedRoute, private getdataService:GetdataService,private http:Http) {
    setInterval(() => {
    //   this.getdataService.getProduct(this.category_url).subscribe((product) => {
    //     this.product=  product;
    //   },
    // err=>{
    //   console.log(err);
    //   return false;
    // });

  }, 5000);
 }

  ngOnInit() {

        this.category_url = this.route.snapshot.paramMap.get('category');
        this.getdataService.getProduct(this.category_url).subscribe((product) => {
          this.product=  product;
        },
      err=>{
        console.log(err);
        return false;
      });
}
}


interface Product{
  _id:number,
  product_name:string,
  image:string,
  image_path:string
}



