import { Component, OnInit } from '@angular/core';

import {RouterModule,ActivatedRoute, Routes} from '@angular/router';
import {GetdataService} from '../../services/getdata.service';


declare var jquery:any;
declare var $ :any;


@Component({
  selector: 'app-fewebsite',
  templateUrl: './fewebsite.component.html',
  styleUrls: ['./fewebsite.component.css']
})
export class FewebsiteComponent implements OnInit {
    public website_url:string;
      website:Website[];
  constructor(private route: ActivatedRoute,private getdataService:GetdataService) { }

  ngOnInit() {
      this.website_url = this.route.snapshot.paramMap.get('website');
      this.getdataService.getWebsitebyID(this.website_url).subscribe((website) => {
        this.website=  website;
        console.log(this.website);
      },
    err=>{
      console.log(err);
      return false;
    });
      $(".head-image").hide();

      $(".backbutton_fromwebsite").on('click',() => {
           $(".head-image").show();
       } );
  }

}

interface Website{
  _id:number,
  Website:string,
  sitename:string,
  image:string,
  image_path:string
}



