import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import { HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';


import { GetdataService } from './services/getdata.service';
import { DeletedataService } from './services/deletedata.service';
import { AdddataService } from './services/adddata.service';
import { SetdataService } from './services/setdata.service';
import { AuthenticationService } from './services/authentication.service';

import { FecategoriesComponent } from './components/fecategories/fecategories.component';
import { FeproductsComponent } from './components/feproducts/feproducts.component';
import { FegalleryComponent } from './components/fegallery/fegallery.component';
import { FemediaComponent } from './components/femedia/femedia.component';
import { FewebsiteComponent } from './components/fewebsite/fewebsite.component';
import { SafePipe } from './components/fewebsite/fewebsite.component.pipe';



import 'materialize-css';
import { MaterializeModule } from 'angular2-materialize';
import { CategoriesComponent } from './components/categories/categories.component';
import { AuthGuard } from './components/guards/auth.guard';
import { ProductsComponent } from './components/products/products.component';
import { GalleryComponent } from './components/gallery/gallery.component';
import { LoginComponent } from './components/login/login.component';
import { AdminComponent } from './components/admin/admin.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { FooterComponent } from './components/footer/footer.component';
import { FemainComponent } from './components/femain/femain.component';
import { MaterialModule } from './material.module';
import { GalleryModalComponent } from './components/gallery-modal/gallery-modal.component';

const appRoutes: Routes = [
  {path:'visiontrucking', component: FemainComponent,
    children:[
      {path:'', component: FecategoriesComponent},
      {path:'fecatetgory', component: FecategoriesComponent},
      {path:'feproducts/:id/:category', component: FeproductsComponent},
      {path:'fegallery/:id/:product_name/:category', component: FegalleryComponent},
      {path:'fewebsite/:website', component: FewebsiteComponent}
    ]
  },
    { path: 'admin', component: AdminComponent,
    children: [
      {path:'', component: CategoriesComponent, canActivate:[AuthGuard]},
      {path:'category', component: CategoriesComponent, canActivate:[AuthGuard]},
      {path:'products/:id/:category', component: ProductsComponent, canActivate:[AuthGuard]},
      {path:'gallery/:id/:product_name/:category', component: GalleryComponent, canActivate:[AuthGuard]},
       {path:'login', component: LoginComponent}
    ]
  }



  



//   { path: 'admin', component: MeetingComponent,
//   children: [
//      { path: 'events', component: EventsComponent, canActivate:[AuthGuard]},
//      { path: 'rooms', component: PlacesComponent, canActivate:[AuthGuard]},
//      { path: 'createaccount', component: SubaccountComponent, canActivate:[AuthGuard]},
//      { path: 'workgroup', component: WorkgroupComponent, canActivate:[AuthGuard]},
//      {path:'login', component: LoginComponent},
//      { path: '**', redirectTo: 'login' }
//   ]
// },
// { path: 'meetingroom/:id', component: FrontMeetingComponent},
// { path: '25774419157095099081/:id', component: FronttotalmeetingsmallComponent},
// { path: 'dualmeeting/:id1/:id2', component: DualMeetingComponent},
// { path: '**', redirectTo: 'admin/events'}



]

@NgModule({
  declarations: [
    AppComponent,
    FecategoriesComponent,
    FeproductsComponent,
    FegalleryComponent,
    FemediaComponent,
    FewebsiteComponent,
    SafePipe,
    AdminComponent,
    CategoriesComponent,
    ProductsComponent,
    GalleryComponent,
    NavbarComponent,
    FooterComponent,
    LoginComponent,
    FemainComponent,
    GalleryModalComponent
  ],
  imports: [
    MaterializeModule,
    HttpModule,
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    MaterialModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [GetdataService,DeletedataService,AdddataService,SetdataService,AuthenticationService,AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }



