import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import {MatSnackBarModule,MatCheckboxModule,MatDialogModule,MatToolbarModule,MatButtonModule,MatCardModule,MatIconModule,MatPaginatorModule,MatInputModule,MatSelectModule, MatMenuModule,} from '@angular/material';
import { MatKeyboardModule } from '@ngx-material-keyboard/core';

@NgModule({
  imports: [
    CommonModule,
    MatButtonModule,
    MatToolbarModule,
    MatIconModule,
    MatCardModule,
    MatPaginatorModule,
    MatInputModule,
    MatSelectModule,
    MatSnackBarModule,
    MatMenuModule,
    MatCheckboxModule,
    MatDialogModule,
    MatKeyboardModule,
    MatSnackBarModule
  ],
  exports:[
    MatButtonModule,
    MatToolbarModule,
    MatButtonModule,
    MatIconModule,
    MatCardModule,
    MatPaginatorModule,
    MatInputModule,
    MatSelectModule,
    MatSnackBarModule,
    MatMenuModule,
    MatCheckboxModule,
    MatDialogModule,
    MatKeyboardModule,
    MatSnackBarModule
  ],
  declarations: []
})
export class MaterialModule { }


