import { Component, OnInit, ElementRef } from '@angular/core';

import {Router, ActivatedRoute} from '@angular/router';

declare var jquery:any;
declare var $ :any;

@Component({
  selector: 'app-femain',
  templateUrl: './femain.component.html',
  styleUrls: ['./femain.component.css']
})
export class FemainComponent implements OnInit {

  constructor(private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    
    var  myclock;
    myclock = setInterval(() => {
        $(".video_fullscreen").show();
         }, 300000);

   $(window).scroll(function() {
      if($(window).scrollTop() + $(window).height() == $(document).height()) {
       $(".bottom_indicator").hide();
     }else{
         $(".bottom_indicator").show();
     }
   });

   $(".bottom_indicator").on('click',() => {
     $("html, body").animate({ scrollTop: $(document).height() }, "slow");
     //this.router.navigateByUrl('/');

   });

   $(".resetCounter").on('click',() => {
     clearTimeout(myclock);
     myclock = setInterval(() => {
         $(".video_fullscreen").show();
          }, 300000);
    } );

    $(".video_fullscreen").on('click',() => {
      //this.router.navigate(['']);
       $(".video_fullscreen").hide();
     } );


  }

}



