import { Component, OnInit, ElementRef } from '@angular/core';
import {GetdataService} from '../../services/getdata.service';
import {MatDialog} from '@angular/material';
import {MatSnackBar} from '@angular/material'
declare var jquery:any;
declare var $ :any;

import {Http, Response, Headers} from '@angular/http';

import {RouterModule,ActivatedRoute, Routes} from '@angular/router';

import 'rxjs/add/operator/map';
import { AdddataService } from '../../services/adddata.service';

@Component({
  selector: 'app-fegallery',
  templateUrl: './fegallery.component.html',
  styleUrls: ['./fegallery.component.css']
})
export class FegalleryComponent implements OnInit {
  gallery:Gallery[];

  public id_url:string;
  public product_url:string;
  public category_url:string;
 // isChecked:boolean;
  isChecked = false;
  checkBoxValue = "";
  //checkedImages:string[];
  checkedImages= [];
  
  constructor(private route: ActivatedRoute, private snackBar:MatSnackBar, private adddataservice:AdddataService, public dialog: MatDialog,private getdataService:GetdataService,private http:Http) {
    
    // $('#room_checkboxes :checked').each(function() {
    //   // this.checkedImages.push($(this).val());
    //   console.log('happened');
    //  });

    setInterval(() => {
      this.getdataService.getGallery(this.id_url).subscribe((gallery) => {
        this.gallery=  gallery;
      },
      err=>{
      console.log(err);
      return false;
      });
    }, 150000);
   }
   

   onChkChange(imageurl:string,index:number,item) {
   // this.isChecked = (this.isChecked === true )? false : true;
    console.log(item)
    console.log(index)
     if( !item.isChecked)
     {
      console.log('is checked');
      console.log( imageurl);
      this.checkedImages.push(imageurl);
      item.isChecked=true;
     }else{
    console.log('is unchecked');
    console.log(imageurl)
      for(let i=0; i<this.checkedImages.length;i++)
      {
        if(this.checkedImages[i] == imageurl){
          this.checkedImages.splice(i,1);
        }
      } 
      item.isChecked=false;
     }
     this.gallery[index].isChecked = item.isChecked;
     console.log(this.checkedImages)
    
   }


  ngOnInit() {

    $(document).ready(function(){
      $('.modal').modal();
    });

    
    $('#keyboard').jkeyboard({
      layout: "english",
      input: $('#email_field')
    });

    this.id_url = this.route.snapshot.paramMap.get('id');
    this.product_url = this.route.snapshot.paramMap.get('product_name');
    this.category_url = this.route.snapshot.paramMap.get('category');

    this.getdataService.getAllGallery(this.id_url).subscribe((gallery) => {
      this.gallery=  gallery;
      for(let i=0; i<this.gallery.length;i++)
      {
        console.log("falsefying")
        this.gallery[i].isChecked = false;
      }
      console.log(this.gallery);
      for(var i=0;i<this.gallery.length;i++)
      {

      //  if(myObj[i].id==product_id)
        {
                if(this.gallery[i].type=="img")
                {
                  console.log("ryan ronalds");
                  $("#gallery").append('  <img alt="'+this.gallery[i].title+'" src="/uploads/'+this.gallery[i].src+'" data-description="'+this.gallery[i].description+'">');


                }
                else if(this.gallery[i].type=="youtube")
                {
                  //  $("#gallery").append('<div data-type="youtube"data-title="'+myObj[i].title+'" data-description="'+myObj[i].description+'"data-thumb="'+myObj[i].href+'" data-image="'+myObj[i].href+'"data-videoid="'+myObj[i].src+'" ></div>  }');
                    $("#gallery").append('<div data-type="youtube" data-videoid="'+this.gallery[i].href+'" data-title="'+this.gallery[i].description+'" data-description="'+this.gallery[i].description+'"></div>}');
              //  $("#gallery").append('<a href="'+myObj[i].href+'"><img src="'+myObj[i].src+'"></a>');

              }
              // console.log("got images "+myObj[i].href)
            }  //  $("#gallery").html5gallery();


        }
        $("#gallery").unitegallery({
            gallery_theme: "tiles",
            gallery_width:"100%",				//gallery width
            gallery_min_width: 150,				//gallery minimal width when resizing
            gallery_background_color: "",		//set custom background color. If not set it will be taken from css.

            tiles_col_width: 500,					//column width - exact or base according the settings
            tiles_align:"center",					//align of the tiles in the space
            tiles_space_between_cols: 3,			//space between images
            tiles_exact_width: false,				//exact width of column - disables the min and max columns
            tiles_space_between_cols_mobile: 3,     //space between cols for mobile type
            tiles_include_padding: true,			//include padding at the sides of the columns, equal to current space between cols
            tiles_min_columns: 2,					//min columns
            tiles_max_columns: 0,					//max columns (0 for unlimited)


            lightbox_textpanel_title_color:null,				//textpanel title color. if null - take from css
            lightbox_textpanel_title_font_family:null,			//textpanel title font family. if null - take from css
            lightbox_textpanel_title_text_align:null,			//textpanel title text align. if null - take from css
            lightbox_textpanel_title_font_size:50,			//textpanel title font size. if null - take from css
            lightbox_textpanel_title_bold:null,					//textpanel title bold. if null - take from css
            lightbox_textpanel_css_title:{},					//textpanel additional css of the title


            lightbox_textpanel_desc_color:null,					//textpanel description font family. if null - take from css
            lightbox_textpanel_desc_font_family:null,			//textpanel description font family. if null - take from css
            lightbox_textpanel_desc_text_align:null,			//textpanel description text align. if null - take from css
            lightbox_textpanel_desc_font_size:50,				//textpanel description font size. if null - take from css
            lightbox_textpanel_desc_bold:null,					//textpanel description bold. if null - take from css
            lightbox_textpanel_css_description:{}
          });
    },
    err=>{
    console.log(err);
    return false;
    });




    /////////////////////////////////////////////
    $(document).ready(function(){

    });
  }

  public sendEmail(): void{

    let formData = new FormData();
    var email_send_address = (<HTMLInputElement>document.getElementById("email_field")).value;
    var JSONcheckedImages = [];
    if(this.checkedImages.length<1)
    {
      JSONcheckedImages[0] = {"url":"","email":email_send_address};
    }
    for(let i=0 ; i<this.checkedImages.length; i++)
    {
      JSONcheckedImages[i]= {"url":this.checkedImages[i],"email":email_send_address};
    }
    console.log(JSONcheckedImages);

    console.log(this.checkedImages);
    let checkedImages = JSON.stringify(this.checkedImages);
    //formData.append('emails',JSONcheckedImages );



    this.adddataservice.sendEmail(JSONcheckedImages).subscribe
    (res =>{ 
      
    // this.submitted=false;
    
    } );

    //resetting checks
    for(let i=0; i<this.gallery.length; i++){
      this.gallery[i].isChecked=false;
    }
    this.checkedImages = [];
    (<HTMLInputElement>document.getElementById("email_field")).value = '';
    this.snackBar.open('Email will be sent if provided data is correct, it may also take a few minutes', 'close');
  }

}

interface Gallery{
  id:number,
  href:string,
  src:string,
  type:string,
  title:string,
  description:string,
  category:string,
  isChecked:boolean
}







