
import { Component, OnInit, ElementRef } from '@angular/core';
import {GetdataService} from '../../services/getdata.service';

import {Http, Response, Headers} from '@angular/http';
import { routerTransition } from '../../components/routeranimation/router.animations';

import {trigger, state, animate, style, transition} from '@angular/animations';


import 'rxjs/add/operator/map';

@Component({
  selector: 'app-fecategories',
  templateUrl: './fecategories.component.html',
  styleUrls: ['./fecategories.component.css'],
  animations: [
   trigger('fadeIn', [
     transition(':enter', [
       style({ opacity: '0' }),
       animate('.5s ease-out', style({ opacity: '1' })),
     ]),
   ]),
  ]
})
export class FecategoriesComponent implements OnInit {
  category:Category[];
  website:Website[];

  constructor(private getdataService:GetdataService, private http:Http) {


   }

  ngOnInit() {

    this.getdataService.getCategory().subscribe((category) => {
      this.category=  category;
    },
  err=>{
    console.log(err);
    return false;
  });

  this.getdataService.getWebsite().subscribe((website) => {
      this.website=  website;
    },
  err=>{
    console.log(err);
    return false;
  });

  }

}

interface Category{
  _id:number,
  item:string,
  file_path:string,
  filename_path:string
}


interface Website{
  _id:number,
  Website:string,
  sitename:string,
  image:string,
  image_path:string
}



